$(".m-tabs li").click(function () {
    let group = $(this).data("group");
    let id = $(this).data("id");

    console.log(group,id)

    $(`.m-tabs li[data-group="${group}"]`).removeClass("active")
    $(this).addClass("active")
    $(`.m-tab[data-group="${group}"]`).hide()
    $(`.m-tab[data-group="${group}"].tab-${id}`).show()
})


$("._all-players").click(function () {
    document.querySelector(".m-tabs li[data-id='all']").click()
})
